body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

.game-container {
  text-align: center;
  padding: 20px;
}

h1 {
  color: #333;
}

.grid-container {
  margin: 20px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(10, 30px);
  grid-template-rows: repeat(10, 30px);
  gap: 2px;
  margin-top: 10px;
  justify-content: center;
}

.grid div {
  width: 30px;
  height: 30px;
  border: 1px solid #ccc;
  background-color: lightblue;
}

.grid .ship {
  background-color: darkblue;
}

.grid .water {
  background-color: lightblue;
}

.grid .hit {
  background-color: red;
}

.grid .miss {
  background-color: lightgray;
}

/* Ship Selection Styles */
#ship-selection-container {
  margin: 20px;
}

#ship-selection .ship {
  padding: 10px;
  margin: 10px;
  background-color: #4CAF50;
  color: white;
  cursor: pointer;
  text-align: center;
  border-radius: 5px;
}

#ship-selection .ship:active {
  background-color: #45a049;
}

/* Game Controls Styles */
.game-controls {
  margin-top: 30px;
}

#start-btn {
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

#start-btn:hover {
  background-color: #45a049;
}

footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #333;
  color: white;
  text-align: center;
  padding: 10px 0;
}
